<template>
  <div>
    <Drawer
      :title="modalData.title"
      v-model="modalData.show"
      width="800"
      draggable
      :mask-closable="false"
    >
      <Form
        ref="formValidate"
        :model="formValidate"
        :rules="ruleValidate"
        :label-width="90"
        style="width: 750px"
      >
        <FormItem
          label="分组名称"
          prop="perm_group_name"
          style="padding-top: 10px"
        >
          <Input
            v-model="formValidate.perm_group_name"
            :maxlength="50"
            placeholder="请输入分组名称"
          ></Input>
        </FormItem>
        <template>
          <Row :gutter="5">
            <FormItem>
              <Alert type="success" show-icon closable>
                根据业务拓扑信息创建动态分组
                <template slot="desc">
                  <p>
                    下列条件的关系为且
                    业务为必填项，集群和模块为可以输入多个，以半角逗号或者空格来分隔。
                  </p>
                  <p>当前类型查询条件不支持通配符，全部为精确查询。</p>
                </template>
              </Alert>
            </FormItem>
            <Col span="12">
              <FormItem label="选择业务" prop="biz_id">
                <Select
                  v-model="formValidate.biz_id"
                  filterable
                  placeholder="请选择业务..."
                  @on-select="handleChangeBiz"
                >
                  <Option
                    v-for="item in bizList"
                    :key="item.biz_id"
                    :value="item.biz_id"
                    :label="item.biz_cn_name"
                  />
                </Select>
              </FormItem>
            </Col>
            <Col span="12">
              <FormItem label="" prop="env_name">
                <Select
                  v-model="formValidate.env_name"
                  filterable
                  placeholder="请选择环境.."
                >
                  <Option
                    v-for="item in envList"
                    :value="item"
                    :key="item"
                    :label="item"
                  ></Option>
                </Select>
              </FormItem>
            </Col>
            <Col span="24">
              <FormItem label="集群名称" prop="region_name">
                <Input
                  v-model="formValidate.region_name"
                  :maxlength="500"
                  placeholder="输入集群、区服名称，多个以半角逗号或者空格区分"
                ></Input>
              </FormItem>
            </Col>
            <!-- <Col span="24">
              <FormItem label="集群名称" prop="region_name">
                <Select
                  v-model="formValidate.region_name"
                  filterable
                  multiple
                  placeholder="请选择集群..."
                >
                  <Option
                    v-for="item in regionList"
                    :key="item.id"
                    :value="item.value"
                    :label="item.value"
                  />
                </Select>
              </FormItem>
            </Col> -->
            <Col span="24">
              <FormItem label="模块名称" prop="module_name">
                <Input
                  v-model="formValidate.module_name"
                  :maxlength="500"
                  placeholder="输入模块、服务名称，多个以半角逗号或者空格区分"
                ></Input>
              </FormItem>
            </Col>
          </Row>
        </template>

        <FormItem label="用户组" prop="user_group">
          <Select
            v-model="formValidate.user_group"
            filterable
            placeholder="请选择用户组"
            multiple
          >
            <Option
              v-for="item in roleList"
              :key="item.id"
              :value="item.role_name"
              :label="item.role_name"
            />
          </Select>
        </FormItem>

        <FormItem label="角色类型" prop="perm_type">
          <RadioGroup v-model="formValidate.perm_type">
            <Radio label="dev">开发</Radio>
            <Radio label="ops">运维</Radio>
          </RadioGroup>
        </FormItem>
        <FormItem label="资产账户" prop="jmss_account">
          <Select
            v-model="formValidate.jmss_account"
            filterable
            placeholder="请选择资产账户"
            multiple
          >
            <Option
              v-for="item in jmssAccounts"
              :key="item"
              :value="item"
              :label="item"
            />
          </Select>
        </FormItem>

        <FormItem label="备注信息" prop="perm_group_detail">
          <Input
            v-model="formValidate.perm_group_detail"
            type="textarea"
            :autosize="{ minRows: 3, maxRows: 5 }"
            placeholder="Enter something..."
          ></Input>
        </FormItem>
        <div class="drawer-footer">
          <Button
            style="margin-right: 8px"
            @click="handleCancel('formValidate')"
            >取消</Button
          >
          <Button type="primary" @click="handleSubmit('formValidate')"
            >提交</Button
          >
        </div>
      </Form>
    </Drawer>
  </div>
</template>

<script>
import { getBizEnv } from '@/api/service-tree';
import { getRoleInfoList, getJmssAccountList } from '@/api/cmdb';

export default {
  name: 'permGroupEdit',
  props: {
    modalData: {
      type: Object,
      default: {},
    },
    bizList: {
      type: Object | Array,
      default: [],
    },
  },
  data() {
    return {
      index: 1,
      btnLoading: false,
      envList: [],
      roleList: [],
      regionList: [],
      jmssAccounts: [],
      formValidate: {
        user_group: [],
        biz_id: null,
        // env_name: null,
        // perm_group_name: '',
        // perm_group_type: '',
      },
      ruleValidate: {
        perm_group_name: [
          { required: true, message: '权限分组名称不能为空', trigger: 'blur' },
        ],
        perm_type: [
          { required: true, message: '请选择角色类型', trigger: 'blur' },
        ],
        user_group: [
          {
            required: true,
            message: '请选择用户组',
            trigger: 'blur',
            type: 'array',
          },
        ],
        jmss_account: [
          {
            required: true,
            message: '请选择堡垒机资产账户',
            trigger: 'blur',
            type: 'array',
          },
        ],
      },
    };
  },
  watch: {
    'formValidate.biz_id': {
      handler(newValue, oldValue) {
        if (newValue && newValue !== oldValue) {
          this.handleGetEnv();
        }
      },
      // immediate: true,
      deep: true,
    },
    //   'formValidate.env_name': {
    //     handler(newValue, oldValue) {
    //       if (newValue && newValue !== oldValue) {
    //         this.handleGetSet();
    //       }
    //     },
    //     immediate: true,
    //     deep: true,
    //   },
  },
  methods: {
    async handleGetEnv() {
      const res = await getBizEnv({ biz_id: this.formValidate.biz_id });
      if (res.code === 0) {
        this.envList = res.data.env_list;
      } else {
        this.$Message.error(res.msg);
      }
    },
    async handleGetJmssAccounts() {
      const res = await getJmssAccountList();
      if (res.code === 0) {
        this.jmssAccounts = res.data;
      } else {
        this.$Message.error(res.msg);
      }
    },
    handleCancel(name) {
      this.$refs[name].resetFields();
      this.$emit('drawer-close');
    },
    handleRoleInfoList() {
      getRoleInfoList().then((res) => {
        if (res.code === 0) {
          this.roleList = res.data;
        } else {
          this.$Message.error(`${res.msg}`);
        }
      });
    },
    handleChange() {
      this.formValidate.biz_id = null;
      this.formValidate.env_name = null;
    },
    handleChangeBiz() {
      this.formValidate.env_name = null;
      this.regionList = [];
      this.envList = [];
      this.formValidate.region_name = null;
    },
    async handleSubmit(name) {
      // 表单验证
      const valid = await this.$refs[name].validate();
      if (!valid) {
        this.$Notice.error({ title: '表单验证失败' });
        return;
      }

      // 验证条件
      setTimeout(() => {
        this.$emit('save-data', this.formValidate, this.modalData.action);
      }, 200);
    },
  },
  //
  mounted() {
    this.handleRoleInfoList();
    this.handleGetJmssAccounts();
  },
  //
};
</script>
<style>
.drawer-footer {
  width: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
  border-top: 1px solid #e8e8e8;
  padding: 10px 16px;
  text-align: right;
  background: #fff;
}
</style>
